import { resolvePath } from '../utils';

export function createService(taskList, {
  image,
  name,
  publishedPort,
  targetPort,
  replicas,
  envFile,
  hostname,
  bind,
  log,
  volumes,
  docker,
  networks,
  hostLabels
}) {
  taskList.executeScript(`Start ${name}`, {
    script: resolvePath(__dirname, 'assets/create-service.sh'),
    vars: {
      name,
      publishedPort,
      targetPort,
      envFile,
      image,
      replicas,
      hostname
    }
  });
}

